@import 'styles/global.scss';
@import 'styles/mixin.scss';

.header {
  height: 9rem;
  .container {
    box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.1);
    background-color: #ffff;
    width: 100%;
    height: 9rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    padding: 0 32rem;
    z-index: 99;
    .logoImg {
      width: 17rem;
      aspect-ratio: 17/5;
    }
    .nav {
      display: flex;
      gap: 3rem;
      align-items: center;
      font-size: 1.2rem;
      font-weight: 400;
      line-height: 1.4rem;
      color: $color-767676;

      a {
        display: inline-block;
        width: 6rem;
        height: 4.6rem;

        img {
          display: block;
          height: 70%;
          margin: auto;
        }

        p {
          text-align: center;
        }
      }
    }
  }
}

@include desktop {
  .header {
    .container {
      padding: 0 15rem;
      .logoImg {
        width: 16rem;
      }
    }
  }
}

@include tablet {
  .header {
    .container {
      padding: 0 10rem;
      .logoImg {
        width: 15rem;
      }
    }
  }
}
